import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import PurchaseReceiptCreateForm from "./createForm";

import notification from "../../../../components/notification";
// import { Input, Select, Divider } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/purchase/purchaseReceipt/actions";
import { Box } from "../../../App/app.style";
import { removeSaveData } from "../../../../helpers/helper";
import { Tabs } from "antd";

const redirecTo = "/dashboard/purchases/purchase-receipts";
const TabPane = Tabs.TabPane;

class CreatePurchaseReceipt extends Component {
  componentDidMount() {
    const { createPurchaseReceipt } = this.props;
    createPurchaseReceipt();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.purchaseReceipt.error) {
      notification("error", this.props.purchaseReceipt.error);
      this.props.clearError();
    }
    if (
      this.props.purchaseReceipt.success &&
      this.props.purchaseReceipt.id_purchase_receipt
    ) {
      this.props.history.replace(
        redirecTo + "/update/" + this.props.purchaseReceipt.id_purchase_receipt
      );
      // this.props.history.replace(redirecTo);
      // notification("success", this.props.purchaseReceipt.success);
      // this.props.clearSuccess();
    }
  }

  handleSubmit = value => {
    const { storePurchaseReceipt } = this.props;
    storePurchaseReceipt(value);
  };

  render() {
    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="purchaseReceipts.button.add" />}>
            <Button
              onClick={() => {
                removeSaveData("pr");
                this.props.history.replace(redirecTo);
              }}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <Tabs defaultActiveKey="1">
            <TabPane tab={<IntlMessages id="jobCostings.tab.detail" />} key="1">
              <PurchaseReceiptCreateForm
                submit={this.handleSubmit}
                purchaseReceipt={this.props.purchaseReceipt}
              />
            </TabPane>
            <TabPane
              tab={<IntlMessages id="jobCostings.tab.attachment" />}
              key="2"
              disabled
            />
          </Tabs>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    purchaseReceipt: state.purchaseReceipt,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(CreatePurchaseReceipt);
