import React, { Component } from "react";
import moment from "moment";
import { connect } from "react-redux";
import { compose } from "redux";

import IntlMessages from "../../../../components/utility/intlMessages";
import actions from "../../../../redux/purchase/purchaseReceipt/actions";
import Button from "../../../../components/uielements/button";
import {
  Form,
  Input,
  DatePicker,
  Select,
  Row,
  Col,
  Empty,
  Tooltip,
  Icon
} from "antd";

import EditableTable from "./editableTable";
import ModalTransaction from "./modal";

// import { Object } from "core-js";

import {
  getSaveData,
  setSaveData,
  removeSaveData
} from "../../../../helpers/helper";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";

const { Option } = Select;
const { TextArea } = Input;
const InputGroup = Input.Group;
const { getTransactions } = actions;

class PurchaseReceiptCreateForm extends Component {
  state = {
    id_receipt_location: null,
    id_supplier: null,
    id_purchase_order: null,
    items: [],
    itemDetails: {
      purchase_receipt_details: []
    },
    total_qty: 0,
    total_receive_qty: 0,
    total_left_qty: 0,
    importedTransaction: 0,
    search: null,
    visible: false,
    errTotRecQty: false,
    err: false,
    err_item: false
  };

  componentDidMount = () => {
    /* Init Restore Autosave */
    let restore = getSaveData("pr");
    if (restore) {
      restore.fields.purchase_receipt_date = moment();
      this.setState({ restore: restore });
    }
    /* END Init Restore Autosave */
  };

  componentDidUpdate = (prevProps, prevState) => {
    const {
      id_receipt_location,
      id_supplier,
      id_purchase_order,
      itemDetails,
      total_qty,
      total_receive_qty,
      total_left_qty,
      importedTransaction,
    } = this.state;
    const { getFieldsValue } = this.props.form;

    /* Restore Autosave */
    if (this.state.restore && prevProps.purchaseReceipt.loadingForeign) {
      const { restore } = this.state;
      const { setFieldsValue } = this.props.form;
      setFieldsValue(restore.fields);
      // console.log(restore);
      this.setState({
        id_receipt_location: restore ? restore.id_receipt_location : null,
        id_supplier: restore ? restore.id_supplier : null,
        id_purchase_order: restore ? restore.id_purchase_order : null,
        items: restore ? restore.purchase_receipt_details : [],
        importedTransaction: restore.importedTransaction,
        restore: null
      });
    }
    /* End Restore Autosave */

    /* Auto Save */
    let data = Object.assign({}, { fields: getFieldsValue() }, itemDetails, {
      id_purchase_order: id_purchase_order,
      id_receipt_location: id_receipt_location,
      id_supplier: id_supplier,
      total_qty: total_qty,
      total_receive_qty: total_receive_qty,
      total_left_qty: total_left_qty,
      importedTransaction
    });

    data.fields.purchase_receipt_date = data.fields.purchase_receipt_date
      ? data.fields.purchase_receipt_date.format("YYYY-MM-DD HH:mm:ss")
      : null;

    if (
      itemDetails.purchase_receipt_details !==
      prevState.itemDetails.purchase_receipt_details
    ) {
      setSaveData("pr", data);
    }

    if (id_receipt_location !== prevState.id_receipt_location) {
      setSaveData("pr", data);
    }

    if (id_supplier !== prevState.id_supplier) {
      setSaveData("pr", data);
    }

    /* END Autosave */
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      const {
        itemDetails,
        id_purchase_order,
        total_qty,
        total_receive_qty,
        total_left_qty
      } = this.state;

      // validasi item
      if (itemDetails.purchase_receipt_details.length <= 0) {
        return this.setState({ err_item: true });
      } else {
        this.setState({ err_item: false });
      }

      if (err) {
        this.setState({ err: true });
        return;
      }

      values.purchase_receipt_date = values.purchase_receipt_date.format(
        "YYYY-MM-DD HH:mm:ss"
      );

      const data = Object.assign({}, values, itemDetails, {
        id_purchase_order: id_purchase_order,
        total_qty: total_qty,
        total_receive_qty: total_receive_qty,
        total_left_qty: total_left_qty
      });
      // console.log(data);
      if (parseFloat(data.total_receive_qty) === 0) {
        this.setState({ errTotRecQty: true });
        return;
      }
      if (!err) {
        this.setState({ errTotRecQty: false, err: false });
        this.props.submit(data);
      }
    });
  };

  handleChangeTransDate = status => {
    if (!status) {
      const { getFieldsValue } = this.props.form;
      const { getTransactions } = this.props;
      
      let { purchase_receipt_date, id_receipt_location, id_supplier } = getFieldsValue([
        "purchase_receipt_date","id_receipt_location", "id_supplier"
      ]);
      purchase_receipt_date = 
        moment(purchase_receipt_date.format("YYYY-MM-DD HH:mm:ss")).format("YYYY-MM-DD HH:mm:ss");
      
      if(id_receipt_location && id_supplier) {
        const filter = 
          `id_supplier=${id_supplier}&id_location=${id_receipt_location}&transaction_date=${purchase_receipt_date}`;
        getTransactions(1, null, filter);
      }
    }
  }

  handleModal = () => {
    const { visible } = this.state;
    visible
      ? this.setState({ visible: false })
      : this.setState({ visible: true });
  };

  handleSelectModal = value => {
    const { transactions } = this.props.purchaseReceipt;
    const { setFieldsValue } = this.props.form;
    const transaction = transactions.find(
      curr_data => curr_data.id_purchase_order === value
    );
    this.setState({
      id_purchase_order: transaction.id_purchase_order,
      id_supplier: transaction.id_supplier,
      id_receipt_location: transaction.id_location,
      items: transaction.purchase_order_details,
      itemDetails: {
        purchase_receipt_details: transaction.purchase_order_details
      }
    });
    setFieldsValue({
      id_receipt_location: transaction.id_location,
      id_supplier: transaction.id_supplier,
      purchase_order_no: transaction.purchase_order_no,
      id_courier: transaction.id_courier
    });
    this.handleModal();
  };

  handleSelectSupplier = value => {
    this.setState({ id_supplier: value });
  };

  handleSelectLocation = value => {
    const { setFieldsValue } = this.props.form;
    this.setState({ id_receipt_location: value, id_supplier: null });
    setFieldsValue({ id_supplier: undefined });
  };

  handleSaveItemList = (items, total_qty, total_received, total_left, importedTransaction = 0) => {
    if (parseFloat(total_received) > 0) {
      this.setState({
        itemDetails: {
          purchase_receipt_details: items
        },
        total_qty: parseFloat(total_qty),
        total_receive_qty: parseFloat(total_received),
        total_left_qty: parseFloat(total_left),
        importedTransaction,
        errTotRecQty: false
      });
    } else {
      this.setState({
        itemDetails: {
          purchase_receipt_details: items
        },
        importedTransaction
      });
    }
  };

  clearItems = (items, total_qty, total_received, total_left, importedTransaction = 0) => {
    this.setState({
      items: [],
      itemDetails: {
        purchase_receipt_details: items
      },
      total_qty: parseFloat(total_qty),
      total_receive_qty: parseFloat(total_received),
      total_left_qty: parseFloat(total_left),
      importedTransaction
    });
  };

  render() {
    // console.log(this.state.itemDetails);
    const { visible, importedTransaction } = this.state;
    const { getFieldDecorator, getFieldValue } = this.props.form;
    const { purchaseReceipt } = this.props;
    const { locations, suppliers, couriers } = purchaseReceipt
      ? purchaseReceipt
      : [];
    const { user } = this.props;
    let { permissions, default_employee_location } = user ? user : [];
    permissions = permissions ? permissions : [];
    default_employee_location = default_employee_location
      ? default_employee_location
      : [];
    const {
      loading,
      loadingSubmit,
      loadingTransactions,
      loadingForeign
    } = purchaseReceipt ? purchaseReceipt : [];
    // const data = purchaseReceipt.purchaseReceipt ? purchaseReceipt.purchaseReceipt : [];

    // const used = data.is_used;
    // const error = purchaseReceipt
    //   ? purchaseReceipt.errorField
    //     ? purchaseReceipt.errorField
    //     : []
    //   : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        <ModalTransaction
          visible={visible}
          purchaseReceipt={purchaseReceipt}
          id_location={getFieldValue("id_receipt_location")}
          id_supplier={getFieldValue("id_supplier")}
          transaction_date={getFieldValue("purchase_receipt_date")
            ? moment(getFieldValue("purchase_receipt_date")).format("YYYY-MM-DD HH:mm:ss")
            : moment().format("YYYY-MM-DD HH:mm:ss")
          }
          handleModal={this.handleModal}
          handleSelectModal={this.handleSelectModal}
        />

        <Row style={{ marginTop: "24px" }}>
          <Col span={12} style={{ paddingRight: "15px" }}>
            <Col span={24}>
              <InputGroup>
                <Col span={12}>
                  <Form.Item
                    {...formItemLayout}
                    label={
                      <IntlMessages id="purchaseReceipts.purchaseReceiptNo" />
                    }
                  >
                    {getFieldDecorator("purchase_receipt_no", {
                      // initialValue: data.purchase_receipt_no,
                    })(<Input disabled={true} />)}
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item
                    {...formItemLayout}
                    label={
                      <Tooltip
                        placement="top"
                        title={
                          <IntlMessages id="transactionDateHelp" />
                        }
                      >
                        <IntlMessages id="purchaseReceipts.receiptDate" />
                        <Icon
                          style={{ marginLeft: "5px" }}
                          type="question-circle"
                          theme="filled"
                        />
                      </Tooltip>
                    }
                    style={{ marginBottom: 0, paddingBottom: 0 }}
                  >
                    {getFieldDecorator("purchase_receipt_date", {
                      initialValue: moment(),
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <DatePicker
                        style={{ width: "100%" }}
                        showTime
                        format="DD MMMM YYYY HH:mm:ss"
                        // onChange={this.handleChangeTransDate}
                        onOpenChange={this.handleChangeTransDate}
                        disabled={
                          !permissions.includes("Can Back Date") || importedTransaction > 0 
                            ? true 
                            : false
                        }
                        allowClear={false}
                        disabledDate={e => e.valueOf() > moment()}
                      />
                    )}
                  </Form.Item>
                </Col>
              </InputGroup>
            </Col>
            <Col span={24}>
              <InputGroup>
                <Col span={12}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="purchaseReceipts.location" />}
                  >
                    {getFieldDecorator("id_receipt_location", {
                      initialValue: default_employee_location.id_location,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <Select
                        showSearch
                        style={{ width: "100%" }}
                        placeholder={
                          <IntlMessages id="purchaseReceipts.location" />
                        }
                        loading={loadingForeign}
                        onSelect={this.handleSelectLocation}
                        // disabled={data.status !== 1}
                        optionFilterProp="children"
                        filterOption={true}
                      >
                        {locations.map((data, key) => (
                          <Option key={key} value={data.id_location}>
                            {`${data.location_code} (${data.location_name})`}
                          </Option>
                        ))}
                      </Select>
                    )}
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="purchaseReceipts.supplier" />}
                  >
                    {getFieldDecorator("id_supplier", {
                      // initialValue: data.id_supplier,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <Select
                        showSearch
                        style={{ width: "100%" }}
                        placeholder={
                          <IntlMessages id="purchaseReceipts.supplier" />
                        }
                        loading={loadingForeign}
                        onSelect={this.handleSelectSupplier}
                        notFoundContent={
                          getFieldValue("id_receipt_location") ? (
                            <Empty description={<IntlMessages id="noData" />} />
                          ) : (
                            <Empty
                              description={<IntlMessages id="noLocation" />}
                            />
                          )
                        }
                        // disabled={data.status !== 1}
                        optionFilterProp="children"
                        filterOption={true}
                      >
                        {suppliers.map((data, key) =>
                          data.id_location ===
                          getFieldValue("id_receipt_location") ? (
                            <Option key={key} value={data.id_supplier}>
                              {`${data.supplier_code} (${data.supplier_name})`}
                            </Option>
                          ) : null
                        )}
                      </Select>
                    )}
                  </Form.Item>
                </Col>
              </InputGroup>
            </Col>
          </Col>
          <Col span={12} style={{ paddingLeft: "15px" }}>
            <Form.Item
              {...formTextArea}
              label={<IntlMessages id="purchaseReceipts.note" />}
            >
              {getFieldDecorator("note", {
                // initialValue: data.note
              })(<TextArea autosize={{ minRows: 4 }} />)}
            </Form.Item>
          </Col>
        </Row>

        <Row>
          <Col span={12} style={{ paddingRight: "15px" }}>
            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={
                    <IntlMessages id="purchaseReceipts.supplierDeliveryNo" />
                  }
                >
                  {getFieldDecorator("supplier_delivery_no", {
                    // initialValue: data.supplier_delivery_no,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(<Input placeholder={getLanguage({ id: "purchaseReceipts.supplierDeliveryNo" })} />)}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={
                    <Tooltip
                      placement="top"
                      title={
                        <IntlMessages id="purchaseReceipts.purchaseOrder.help" />
                      }
                    >
                      <IntlMessages id="purchaseReceipts.purchaseOrder" />
                      <Icon
                        style={{ marginLeft: "5px" }}
                        type="question-circle"
                        theme="filled"
                      />
                    </Tooltip>
                  }
                >
                  <Button
                    icon="select"
                    loading={loadingTransactions}
                    disabled={
                      loadingTransactions ||
                      !getFieldValue("id_receipt_location") ||
                      !getFieldValue("id_supplier")
                    }
                    onClick={this.handleModal}
                    style={{ height: "31px", width: "100%" }}
                  >
                    <IntlMessages id="button.import" />
                  </Button>
                </Form.Item>
              </Col>
            </InputGroup>
          </Col>

          <Col span={12} style={{ paddingLeft: "15px" }}>
            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="purchaseOrders.purchaseOrderNo" />}
                >
                  {getFieldDecorator("purchase_order_no", {
                    // initialValue: data.purchase_order_no,
                  })(<Input disabled={true} />)}
                </Form.Item>
              </Col>

              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="itemTransfers.courier" />}
                >
                  {getFieldDecorator("id_courier", {
                    // initialValue: data.id_courier,
                  })(
                    <Select
                      showSearch
                      style={{ width: "100%" }}
                      // placeholder={<IntlMessages id="itemTransfers.courier" />}
                      loading={loadingForeign}
                      optionFilterProp="children"
                      filterOption={true}
                      disabled={true}
                    >
                      {couriers.map((data, key) => (
                        <Option key={key} value={data.id_courier}>
                          {`${data.courier_name} (${data.description})`}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>
          </Col>
        </Row>

        <Row style={{ marginBottom: "30px" }}>
          <Col span={24}>
            <EditableTable
              items={this.state.items}
              handleSaveItemList={this.handleSaveItemList}
              clearItems={this.clearItems}
              loadingTransactions={loadingTransactions}
            />
          </Col>
        </Row>

        <Row>
          <Col span={14} offset={10} style={{ paddingLeft: "15px" }}>
            <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
              <Col span={15} style={{ marginTop: "5px", textAlign: "right" }}>
                <IntlMessages id="purchaseReceipts.totalQty" />
                <span>
                  <IntlMessages id="bsUnt">{txt => ` (${txt})`}</IntlMessages>
                </span>
              </Col>
              <Col span={8} offset={1} style={{ marginTop: "5px", textAlign: "right" }}>
                <span>
                  <NumberFormat value={this.state.total_qty} />
                </span>
              </Col>
            </Row>

            <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
              <Col span={15} style={{ marginTop: "5px", textAlign: "right" }}>
                <IntlMessages id="purchaseReceipts.totalReceiveQty" />
                <span>
                  <IntlMessages id="bsUnt">{txt => ` (${txt})`}</IntlMessages>
                </span>
              </Col>
              <Col span={8} offset={1} style={{ marginTop: "5px", textAlign: "right" }}>
                <span>
                  <NumberFormat value={this.state.total_receive_qty} />
                </span>
              </Col>
            </Row>

            <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
              <Col span={15} style={{ marginTop: "5px", textAlign: "right" }}>
                <strong>
                  <IntlMessages id="purchaseReceipts.totalLeftQty" />
                  <span>
                    <IntlMessages id="bsUnt">{txt => ` (${txt})`}</IntlMessages>
                  </span>
                </strong>
              </Col>
              <Col span={8} offset={1} style={{ marginTop: "5px", textAlign: "right" }}>
                <span>
                  <NumberFormat value={this.state.total_qty} />
                </span>
              </Col>
            </Row>
          </Col>
        </Row>

        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "24px" }}
            loading={loading || loadingSubmit}
            disabled={
              this.state.errTotRecQty ||
              !permissions.includes("Create Purchase Receipt")
            }
            onClick={() => removeSaveData("pr")}
          >
            <IntlMessages id="button.submit" />
          </Button>
          
          <br />

          {this.state.err ? (
            <span style={{ color: "#f5222d" }}>
              <IntlMessages id="form.error" />
            </span>
          ) : null}

          {this.state.err_item ? (
            <IntlMessages id="itemDataEmpty">
              {txt => <span style={{ color: "#f5222d" }}>{txt}</span>}
            </IntlMessages>
          ) : null}
        </Form.Item>
      </Form>
    );
  }
}

const WrappedPurchaseReceiptCreateForm = Form.create({
  name: "create_purchase_order"
})(PurchaseReceiptCreateForm);

const mapStateToProps = state => {
  return {
    user: state.auth.user,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    { getTransactions }
  )
)(WrappedPurchaseReceiptCreateForm);
